import React, { Component } from 'react';
import {Link} from 'react-router-dom';

import './index.css';

/**
 * @Author: miansen
 * @Date: 2018/12/16
 * @description: 
 */
class About extends Component{
    render() {
        return (
            <div className="about-struct bui-box">
                <div className="bui-box">
                    <div className="pane-module">
                        <div className="column-wrap"><span></span></div>
                        <ul className="module-content about-list">
                            <li className="item"><Link to="/about"></Link></li>
                            <li className="item"><Link to="/about"></Link></li>
                            <li className="item"><Link to="/about"></Link></li>
                        </ul>
                        <div className="company">
                            <p className="J-company-name">  2018 TELL</p>
                            <div>
                                <a href="http://www.miibeian.gov.cn/" target="_blank" ga_event="click_about">ICP18002390-5</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default About;